<template>
<div class="tabbar">
  <ul>
    <li v-for="(item,index) in routerList" :key="index" @click="switchTab(item.path)">
      <img :src="$route.path.includes(item.path)?item.selected:item.active" alt="">
      <span :class="$route.path.includes(item.path)?'active':''">{{item.title}}</span>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  data(){
    return{
      routerList:[
        {
          title:"首页",
          path:"/home",
          active:"./imgs/首页.png",
          selected:"./imgs/首页-选中.png"
        },
        {
          title:"分类",
          path:"/List",
          active:"./imgs/首页.png",
          selected:"./imgs/首页-选中.png"
        },
        {
          title:"购物车",
          path:"/Cart",
          active:"./imgs/首页.png",
          selected:"./imgs/首页-选中.png"
        },
        {
          title:"我的",
          path:"/My",
          active:"./imgs/首页.png",
          selected:"./imgs/首页-选中.png"
        }
      ]
    }
  },
  methods:{
    switchTab(path){
      // 判断路由
      if ( this.$route.path == path){
        return false;
      }else{
        this.$router.replace(path)
        // console.log(path);
      }

    }
  }
};
</script>

<style scoped>
.tabbar{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 55px;
background-color: azure;
}
.tabbar ul{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
}
.tabbar ul li{
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}
.tabbar ul img{
  width: 25px;

}
.tabbar ul li span{
  font-size: 16px;
}
.active{
  color: red;
}
</style>